.form {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    max-width: 400px;
    padding: 15px;
    margin-left: 30px;
    background-color: #fcfcfc;
  
    border: 1px solid #969696;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  }
  
  .label {
    margin-bottom: 5px;
  
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
  }
  
  .input {
    height: 20px;
    width: 70%;
    margin-bottom: 20px;
  }
  
  .button {
    height: 30px;
  
    border: solid 1px rgb(115, 114, 114);
    border-radius: 6px;
    cursor: pointer;
    box-shadow: -5px -5px 23px -13px rgba(34, 60, 80, 0.6) inset;
  }
  
  .button:hover {
    background-color: rgb(182, 190, 249);
    border: solid 1px rgb(115, 114, 114);
    border-radius: 6px;
    box-shadow: -5px -5px 23px -13px rgba(34, 60, 80, 0.6) inset;
  }