.item {
    display: flex;
    justify-content: space-around;
    align-items: center;
  
    font-size: 18px;
    padding: 8px;
    background-color: #fcfcfc;
  
    border: solid 1px rgb(115, 114, 114);
    box-shadow: 0px 5px 15px -6px rgba(34, 60, 80, 0.2);
  }
  
  .item:not(:last-child) {
    margin-bottom: 10px;
  }
  
  .button {
    height: 25px;
  
    border: solid 1px rgb(115, 114, 114);
    border-radius: 6px;
    cursor: pointer;
    box-shadow: -5px -5px 23px -13px rgba(34, 60, 80, 0.6) inset;
  }
  
  .button:hover {
    background-color: rgb(182, 190, 249);
    border: solid 1px rgb(115, 114, 114);
    border-radius: 6px;
    box-shadow: -5px -5px 23px -13px rgba(34, 60, 80, 0.6) inset;
  }
  
  .name {
    font-weight: 500;
    margin-right: 15px;
  }
  
  .number {
    margin-right: auto;
  }